<template>
  <div
    v-if="purchase"
    v-loading="loading"
    class="main-box"
    element-loading-text=""
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <template v-if="pageInfo">
      <el-card class="box-card-rt" shadow="never">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <el-descriptions :column="2">
          <el-descriptions-item label="">
            {{ pageInfo.contractCode || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="">
            {{ pageInfo.contractDate || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="">
            {{ pageInfo.supplierCode || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="">
            {{ pageInfo.supplierName || '---' }}
          </el-descriptions-item>
        </el-descriptions>
      </el-card>
      <el-card class="box-card-rb" shadow="never">
        <div slot="header" class="clearfix">
          <span></span>
          <!--  -->
          <el-link :disabled="['2', '3'].includes(extraInfo.materialInfo.deliveryReceiptType)" :type="['2', '3'].includes(extraInfo.materialInfo.deliveryReceiptType) ? 'info' : 'primary'" :underline="false" class="fr" @click="handleViewOrder"></el-link>
        </div>
        <el-descriptions>
          <el-descriptions-item label="">
            <!--  -->
            <el-link v-if="pageInfo.orderCode" :disabled="['2', '3'].includes(extraInfo.materialInfo.deliveryReceiptType)" :type="['2', '3'].includes(extraInfo.materialInfo.deliveryReceiptType) ? 'info' : 'primary'" :underline="false" @click="handleViewPurchaseOrder">{{ pageInfo.orderCode }}</el-link>
            <span v-else>---</span>
          </el-descriptions-item>
          <el-descriptions-item label="">
            {{ pageInfo.purchaseDate || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="">
            {{ pageInfo.materialsUnit || '---' }}
          </el-descriptions-item>
        </el-descriptions>
        <table-com
          ref="baseTableRef"
          :cell-style="handleCellStyle"
          :refresh-key="refreshKey"
          :table-height-type="350"
          :loading="false"
          :index="true"
          :index-fixed="false"
          :selection="false"
          :multiple="false"
          :selection-fixed="false"
          :table-column-sort="false"
          :table-data="pageInfo.orderMaterial || []"
          :column-list="columnList"
          :show-operate="showOperate"
          :operate-fixed="false"
          :table-selection="tableSelection"
          :operation-width="100"
          @selectionChange="selectionChange"
        />
      </el-card>
    </template>
    <!-- <template v-else>
      <el-card class="box-card-rt-empty" shadow="never">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        
      </el-card>
    </template> -->
  </div>
  <el-empty v-else class="empty-sty">
    <template slot="description">
      <el-button type="text" style="font-size: 23px;">
        
      </el-button>
    </template>
  </el-empty>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getBarCodePurchaseApi } from '@/apis/modules/trace'
export default {
  name: 'Purchase',
  components: {
  },
  mixins: [paginationPage],
  props: {
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    purchase: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      refreshKey: 1, // key
      showOperate: false, // 
      loading: false,
      tableSelection: [], // 
      columnList: [
        {
          module: 'word',
          prop: 'materialCode',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialSpec',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'orderNum',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'price',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitMaterial',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      pageInfo: null
    }
  },
  computed: {
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      const params = {
        ...this.extraInfo
      }
      console.log('tabs-', params)
      this.loading = true
      return new Promise((resolve, reject) => {
        getBarCodePurchaseApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.pageInfo = data
              console.log('tabs-', data)
            }
            this.loading = false
            resolve(res)
          })
          .catch(err => {
            console.log('err', err)
            this.loading = false
            reject(err)
          })
      })
    },
    // Tip 
    handleStepClick(step) {},
    // Tip 
    handleViewOrder() {
      this.$router.push({
        name: 'InStoragePurchase',
        params: {
          // Tip 
          contractCode: this.pageInfo.contractCode || ''
        }
      })
    },
    // Tip 
    handleViewPurchaseOrder() {
      this.$router.push({
        name: 'InStoragePurchase',
        params: {
          // Tip 
          contractCode: this.pageInfo.contractCode || '',
          // Tip 
          orderCode: this.pageInfo.orderCode || ''
        }
      })
    },
    // Tip 
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    handleCellStyle({ row }) {
      const cellStyle = {
        backgroundColor: row.color
      }
      return cellStyle
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .box-card-rt {
  border-width: 0 0 1px 0;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
::v-deep .box-card-rt-empty {
  border: none;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
::v-deep .box-card-rb {
  margin-top: 20px;
  border: none;
  .el-card__header {
    padding: 0px 20px;
    border: none;
  }
}
.main-box {
  min-height: 620px;
  padding: 10px;
  box-sizing: border-box;
}
.empty-sty {
  height: 580px;
  line-height: 580px;
  padding: 0;
  ::v-deep .el-empty__image {
    display: none;
  }
}
</style>

