<template>
  <div class="app-container">
    <div class="clearfix top-filter">
      <el-tabs v-model="activeTabsName" type="card">
        <el-tab-pane label="" name="barcode">
          <bar-code ref="barcode" :extra-info="{...query}" @switchBatchNoTabPanel="switchBatchNoTabPanel" />
        </el-tab-pane>
        <el-tab-pane label="" name="material">
          <material ref="material" :extra-info="{...query}" @switchBatchNoTabPanel="switchBatchNoTabPanel" />
        </el-tab-pane>
        <el-tab-pane label="" name="batchNo">
          <batch-no ref="batchNo" :extra-info="{...query}" @switchBarcodeTabPanel="switchBarcodeTabPanel" />
        </el-tab-pane>
        <el-tab-pane label="" name="supplier">
          <supplier ref="supplier" :extra-info="{...query}" />
        </el-tab-pane>
        <el-tab-pane label="" name="serialNo">
          <serial-no ref="serialNo" :extra-info="{...query}" @switchBarcodeTabPanel="switchBarcodeTabPanel" />
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { BarCode, Material, BatchNo, Supplier, SerialNo } from './components'
export default {
  name: 'SearchResult',
  components: {
    BarCode,
    Material,
    BatchNo,
    Supplier,
    SerialNo
  },
  mixins: [],
  data() {
    return {
      title: this.$route.meta.title,
      query: {
        // start: '',
        // end: ''
      },
      activeTabsName: 'barcode'
    }
  },
  computed: {
  },
  mounted() {
  },
  methods: {
    // Tip (tab)
    switchBarcodeTabPanel(row) {
      console.log('row', row)
      this.activeTabsName = 'barcode'
      this.$nextTick(() => {
        // Tip tab
        this.$refs['barcode'].query.nameOrCode = row.barCode || ''
        // Tip 
        this.$refs['barcode'].search()
      })
    },
    // Tip (tab)
    switchBatchNoTabPanel(row) {
      console.log('row', row)
      this.activeTabsName = 'batchNo'
      this.$nextTick(() => {
        // Tip tab
        this.$refs['batchNo'].query.nameOrCode = row.batchNo || ''
        // Tip 
        this.$refs['batchNo'].init()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
</style>

